  font-size: 12px;
}

.header__contact p:not(.header__tel) {
  text-align: center;
}

.header__contact a {
  font-size: 20px;
  color: #1d1d1d;
  transition: 0.3s color ease;
}

.header__contact a:hover {
  color: #ffd900;
}

.header__burger {
  position: absolute;
  top: 0;
  right: 20px;
  display: none;
  height: auto;
  width: 28px;
  background-color: transparent;
  z-index: 11;
}

@media only screen and (max-width: 1100px) {
  .header__burger {
    display: block;
  }
  .header__burger--none {
    display: none;
  }
}

.header__burger span {
  transition: 0.3s;
  display: block;
  height: 3px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 10px;
}

.header__burger span:not(:last-child) {
  margin-bottom: 5px;
}

.header__burger.is-active {
  position: absolute;
  right: 10px;
  top: 0px;
  z-index: 150;
}

.header__burger.is-active span:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
}

.header__burger.is-active span:nth-child(2) {
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
}

.header__burger.is-active span:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

@media only screen and (max-width: 1100px) {
