.slider {
  width: 100%;
  height: 100%;
}

.slider__slide {
  position: relative;
  width: 100%;
  height: 600px;
}

.slider__slide::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.slider .slick-arrow {
  position: absolute;
  bottom: 50%;
  left: 100px;
  width: 25px;
  height: 40px;
  transform: translateY(50%);
  font-size: 0;
  z-index: 5;
  background-color: transparent;
}

.slider .slick-arrow:before, .slider .slick-arrow:after {
  content: '';
  position: absolute;
  top: 16px;
  left: 10px;
  display: block;
  width: 3px;
  height: 20px;
  background-color: #ffffff;
  transform: rotate(45deg);
  transition: 0.4s background-color ease;
  border-radius: 1px;
}

.slider .slick-arrow:hover::before, .slider .slick-arrow:hover::after {
  background-color: #ffd900;
}

.slider .slick-next:before, .slider .slick-next:after {
  right: 10px;
  left: auto;
}

.slider .slick-arrow:after {
  transform: rotate(-45deg);
  top: 4px;
}

.slider .slick-prev:after {
  transform: rotate(-135deg);
}

.slider .slick-prev:before {
  transform: rotate(135deg);
}

.slider .slick-next {
  left: auto;
  right: 100px;
}

.slider .slick-dots {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.slider .slick-dots li {
  display: flex;
  margin-right: 20px;
}

.slider .slick-dots button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border: 1px solid #ffd900;
  background: #ffffff;
  border-radius: 50%;
}

.slider .slick-dots .slick-active {
  position: relative;
}

.slider .slick-dots .slick-active button:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: #ffd900;
  border-radius: 50%;
}

.slider .slick-slide > div {
  display: flex;
}

@media only screen and (max-width: 1400px) {
  .slider__slide {
    height: 400px;
  }
  .slider .slick-dots {
    bottom: 30px;
  }
}

@media only screen and (max-width: 724px) {
  .slider .slick-arrow {
    display: none !important;
  }
}

