.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
}

.popup__overflow {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(29, 29, 29, 0.7);
}

.popup.is-show {
  display: block;
}

.popup__content {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 50px;
  background-color: #ffffff;
  border-radius: 5px;
  z-index: 1;
}

.popup__close {
  position: absolute;
  top: 50px;
  right: 50px;
  font-size: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: 0.3s all ease;
}

.popup__close:before, .popup__close:after {
  position: absolute;
  top: 20px;
  right: 10px;
  content: '';
  display: block;
  width: 20px;
  height: 2px;
  background-color: #1d1d1d;
  transform: rotate(45deg);
  transition: 0.3s all ease;
}

.popup__close:after {
  transform: rotate(-45deg);
}

.popup__close:hover {
  transform: scale(1.2);
}

.popup__close:hover:after, .popup__close:hover:before {
  background-color: #c01a3c;
}

.popup__btn {
  display: flex;
  justify-content: center;
}
