.footer {
    position: relative;
    background-color: $black;
    color: $white;
    margin-top: 60px;
    z-index: 2;

    &__inner {
        display: flex;
        justify-content: space-between;
        padding: 40px 0; }

    &__contact {
        display: flex;
        flex-direction: column;
        // align-items: center
        font-size: 12px;

        a {
            font-size: 20px;
            color: $white;
            transition: 0.3s color ease;

            &:hover {
                color: $gold; } } }

    &__tel {
        font-size: 18px;
        color: $gold;
        margin-bottom: 5px; }
    &__menu {
        a {
            font-size: 14px;
            color: $white;
            &:hover {
                color: $gold; } } }

    // &__social .social

    //     position: absolute
    //     top: 50%
    //     right: 0
    //     transform: translateY(-50%)

    //     &__link:hover
    //         svg
    //             fill: $gold

    //     svg
    //         fill: $white

    &__copyright {
        padding-bottom: 10px;
        font-size: 12px;
        text-align: center;
        color: $white;
        b {
            font-size: 14px;
            @include fontRM; } }

    h3 {
        @include fontRM;
        font-size: 18px;
        color: $gold;
        margin-bottom: 10px; }

    @include r(900) {

        &__inner {
            flex-direction: column;
            align-items: center; }

        &__menu {
            display: none; }

        &__contact {
            text-align: center;
            margin-bottom: 20px; } }

    @include r(724) {

        &__map {
            iframe {
                width: 100%; } } } }

